import React, {Component} from 'react'
import styled from 'styled-components'
import {primaryColor} from '~/src/styles'

const Footer = styled.footer`
  min-height: 80px;
  border-top: 10px solid ${primaryColor};
`

class BlogFooter extends Component {
  render () {
    return (
      <Footer>
        <p>
          Hi! I'm Nathan. I'd love to hear from you, whether you have a question, a comment, or just want to say hi :)
        </p>
        <p>
          By the way, I started this blog at the Recurse Center, a fantastic programming community that I recommend highly.
        </p>
        <div className='rc-scout'>
          <p class="rc-scout__text"><i class="rc-scout__logo"></i> Want to become a better programmer? <a class="rc-scout__link" href="https://www.recurse.com/scout/click?t=588dda8f4f69c79a63576ddde074a24f">Join the Recurse Center!</a></p>
        </div>
        <p>
          © Nathan Shane
        </p>
        <script async defer src="https://www.recurse-scout.com/loader.js?t=588dda8f4f69c79a63576ddde074a24f"></script>
      </Footer>
    )
  }
}

export default BlogFooter



